/* eslint-disable react/jsx-no-duplicate-props */
/* eslint-disable max-len */
import {
  Field,
  Form,
  Formik,
} from 'formik';
import React, { useState } from 'react';
import { teamValidate, validateCity } from '../utilits/validators';
import './Search-Form.css';

const SearchForm: React.FC<ISearchFormProps> = ({ handleAdd }) => {
  const [inputValue, setInputValue] = useState<string>('');
  const handleChange = (e: React.FormEvent<HTMLInputElement>) => {
    setInputValue((e.currentTarget.value).toUpperCase());
  };
  return (
    <div className="form-container">
      <h2>Teams</h2>
      <Formik
        initialValues={{
          name: '',
          city: '',
          abbreviation: '',
          conference: 'East',
          id: 0,
        }}
        onSubmit={(values: IValuesFromType) => {
          const newTeam = {
            id: Math.floor(Math.random() * 10000),
            name: values.name,
            city: values.city,
            abbreviation: inputValue,
            conference: values.conference,
          };
          handleAdd(newTeam);
        }}
      >
        {({ errors, touched, validateOnChange }) => (
          <Form>
            <div className="add-form">
              <div>
                <span>Team</span>
                <br />
                <Field
                  type="text"
                  name="name"
                  placeholder="Team"
                  className={errors.name && touched.name ? 'set-input error' : 'set-input'}
                  validate={teamValidate}
                />
                {errors.name && touched.name && <div className="error-city">{errors.name}</div>}
              </div>
              <div>
                <span>City</span>
                <br />
                <Field
                  type="text"
                  name="city"
                  placeholder="City"
                  className={errors.city && touched.city ? 'set-input error' : 'set-input'}
                  validate={validateCity}
                />
                {errors.city && touched.city && <div className="error-city">{errors.city}</div>}
              </div>
              <div>
                <span>Abbreviation</span>
                <br />
                <Field
                  type="text"
                  placeholder="Abbreviation"
                  className={touched.abbreviation && !inputValue ? 'set-input error' : 'set-input'}
                  name="abbreviation"
                  value={inputValue}
                  onChange={(e: React.FormEvent<HTMLInputElement>) => handleChange(e)}
                />
                {touched.abbreviation && !inputValue && <div className="error-city">Field is required</div>}
              </div>
              <div>
                <span>Conference</span>
                <br />
                <Field as="select" name="conference" className="set-input">
                  <option value="East">East</option>
                  <option value="West">West</option>
                </Field>
              </div>
              <div className="btn-container">
                <button type="submit" className="add-btn">Add Team</button>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default SearchForm;

type ISearchFormProps = {
  handleAdd: (newTeam: IValuesFromType) => void
}

export type IValuesFromType = {
  name: string
  city: string
  abbreviation: string
  conference: string
  id: number
}
